.menu--mobile {
  height: 60px;
  padding: 0 15px;
  display: flex;
  flex-direction:row;
  justify-content: space-between;
  align-items: center;
  background: #2C2F38;
  position: fixed;
  z-index: 3;
  width: 100%;
  top: 0;
  left: 0;
  border-bottom: 1px solid rgba(255,255,255,0.12);
  .burger {
    cursor:pointer;
  }
}

.dashboard--mobile {
  padding-top: 60px;
  .filterPanel {
    margin-bottom: 0 !important;
  }
}

.filterPanel {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items:center;
  margin-bottom: 20px;
  position: relative;

  & > div {
    margin-right: 20px;
  }
  .MuiFilledInput-root {
    height: 56px;
  }
  // #393d48
  .filterPanel__select {
    background: #3f434d;
    border-radius: 4px 4px 0px 0px;
    overflow:hidden;
    margin-right: 2px;
    height: 56px;
    min-width: 210px;
    .MuiFormControl-root label {
      font-size: 13px;
    }
  }

  .filterPanel__add {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    width: 56px;
    min-width:unset !important;
    box-sizing:border-box;
    height: 56px;
    border-radius:50%;
    background: #A1AFDB;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2), 0px 1px 10px rgba(0, 0, 0, 0.12), 0px 4px 5px rgba(0, 0, 0, 0.14);
    cursor:pointer;
    margin-right: 0;
  }
  .filterPanel__edit {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    min-width: 40px;
    height: 40px;
    border-radius:50%;
    background: #414552;
    cursor:pointer;
    margin-right: 12px;
  }
  .filterPanel__select__order {
    min-width: 56px;
    background: #434753;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    border-radius: 4px 4px 0px 0px;
    height: 56px;
    border-bottom: 1px solid rgba(255,255,255,0.67);
    cursor:pointer;
    transition-duration:.3s;
    &:hover {
      background-color: #3e4350;
      transition-duration:.3s;
    }
    &--active {
      background: #363b45;
      &:hover {
        background: #343842;
      }
    }

  }
  &__doubled {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
    & > div {
      margin-right: 2px;
    }
  }
  .w160 {
    width: 160px;
  }
  .w120 {
    width: 120px;
  }
  .w100percent {
    width: 100%;
  }
  .MuiTextField-root {
    margin-right: 0;
  }
  .MuiSelect-root {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
    padding-left: 14px;
    font-size: 15px;
    padding-top: 21px;
    letter-spacing: 0.1px;
    svg {
      margin-right: 10px;
      margin-top: -17px;
    }
  }
  .custom_label {
    .MuiInputLabel-filled.MuiInputLabel-shrink {
      transform: translate(48px, 10px) scale(0.75);
      font-size: 16px;
      margin-left: 36px;
    }
    svg {
      width: 24px;
    }
  }
  label {
    font-size: 16px;
  }
  .search_area {
    border-radius: 4px 4px 0px 0px;
    overflow:hidden;
    input {
      padding: 27px 12px 10px;
      height: 56px;
      font-weight: 300;
      box-sizing: border-box;
      padding-top: 10px;
      padding-left: 52px;
      font-size: 15px;
      background: #434753 url('../../../Images/search.svg') 15px 15px no-repeat;
      &::placeholder {
        font-size: 15px;
        letter-spacing:0.5px;
      }
    }
  }
  .search_additional_components {
    position: absolute;
    right: 3px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
    margin-right: 0;
    div {
      margin-right: 13px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items:center;
    }
  }
}

.ÑustomStyle .MuiAccordion-rounded {
  background-color: #2C2F38 !important;

  &.Mui-expanded {
    background-color: #343843 !important;
    * {
      color: rgba(255, 255, 255, 1);
    }
  }

  * {
    color: rgba(255, 255, 255, 0.6) ;
  }
}


.MuiAccordion-root {
  width: calc(100% + 30px);
  margin-left: -15px !important;
  margin-bottom: 0;


  &.Mui-expanded {
    margin: 0 !important;
    margin-left: -15px !important;
  }
  .MuiAccordionSummary-root.Mui-expanded {
    min-height:auto !important;
    height: 48px;
  }
  .MuiAccordionSummary-content.Mui-expanded {
    margin: 12px 0 !important;
  }
  .MuiAccordionDetails-root {
    position: relative;
    padding: 0 15px 15px;
    &:before {
      content: ' ';
      position:absolute;
      height: 1px;
      background: rgba(255,255,255,0.1);
      width: calc( 100% - 30px );
      top: 0;
      left: 15px;
    }
  }
}

.day-row {
  height: 50px;
  display: flex;
  flex-direction: row;

  align-items:center;
}

.accordion-data-description {
  display: flex;
  width: 100%;
  color: rgba(255, 255, 255, 0.6) !important;
  margin-top: 15px;
}
.MuiAccordionDetails-root {
  display: flex;
  flex-wrap:wrap;
  padding: 8px 15px 15px;
}




.filterPanel--mobile {
  &.filterPanel--users {
    flex-wrap:wrap;
    & > div {
      margin-right: 0;
    }
  }
  .search_area input,
  .MuiFilledInput-root,
  .filterPanel__select,
  .filterPanel__select__order,
  .filterPanel__add{
    height: 48px;
  }
  .filterPanel__add {
    width: 48px;
    margin-left: auto;
  }
  .filterPanel__select__order {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
    margin-left: 2px;
  }
  .filterPanel__select {
    width: calc(100% - 117px);
  }

  .MuiSelect-root {
    padding-left: 11px;
  }
}