// Colors
$darkGrey : #16181C;

$grey : #2C2F38;
$lightGrey : rgba(67,71,83, 0.5);
$blue : #A1AFDB;
$red:  #D8635B;
$green: #519D64;

// Componentns
$boxShadow : 0px 2px 4px rgba(12, 13, 15, 0.2), 0px 1px 10px rgba(12, 13, 15, 0.12), 0px 4px 5px rgba(12, 13, 15, 0.14);
$borderRadius : 6px;


// Text
$colorLight : rgba(255, 255, 255, 0.87);
$colorDark : rgba(255, 255, 255, 0.6);

.color-white{
  color: $colorLight !important;
}
.color-grey{
  color: $colorDark !important;
}
.color-blue{
  color: $blue !important;
}
.color-orange {
  color: #F2994A !important;
}
.white {
  color: white;
}
.color-red {
  color: $red !important;
}
.color-green {
  color: $green !important;
}


